import React, { Component } from 'react';
import ParkingListItem from './ParkingListItem';

class ParkingList extends Component {
  render() {
    if (this.props.shortlists.length < 1) return;
    return (
      <div className="container-lg px-0 pb-2">
        <div className="list-group list-group-flush fs-5">
          <ParkingListItem carparkData={(this.props.shortlists.length > 0) ? this.props.shortlists[0] : null} />
          <ParkingListItem carparkData={(this.props.shortlists.length > 1) ? this.props.shortlists[1] : null} />
          <ParkingListItem carparkData={(this.props.shortlists.length > 2) ? this.props.shortlists[2] : null} />
          <ParkingListItem carparkData={(this.props.shortlists.length > 3) ? this.props.shortlists[3] : null} />
          <ParkingListItem carparkData={(this.props.shortlists.length > 4) ? this.props.shortlists[4] : null} />
        </div>
      </div>
    );
  }
}

export default ParkingList;