export class Availability {
  constructor(onDataLoadCallback) {
    this.data = null;
    this.refreshDateTime = null;
    this.dataError = null;
    this.dataLoad = false;
    this.onDataLoadCallback = onDataLoadCallback;

    this.fetchAvailabilityData();
  }

  fetchAvailabilityData = () => {
    const url = 'https://www.koleksikatik.com/data/d.json';
    const method = 'GET';
    const headers = { 'content-type': 'application/json' };

    fetch(url, { method: method, headers })
    .then(response => { return response.json() })
    .then((data) => {
      this.data = data.items[0].carpark_data;
      this.refreshDateTime = data.items[0].timestamp;
      this.dataLoad = true;
      this.onDataLoadCallback({ timestamp: this.refreshDateTime, data: this.data });
    }).catch((error) => {
      this.dataError = error;
      this.dataLoad = true;
    });
  }
}
