import "./App.css";
import PlacesAutocomplete from "./components/PlacesAutocomplete";
import DistanceMatrixWrapper from "./components/DistanceMatrixWrapper";
import ParkingPeriod from "./components/ParkingPeriod";
import ParkingTimeOffset from "./components/ParkingTimeOffset";
import ParkingList from "./components/ParkingList";
import React from "react";
import { Availability } from "./lib/Availability";
import { CarparkShortlist } from "./lib/CarparkShortlist";

class App extends React.Component {
  state = {
    position: null,
    distance: null,
    availability: {},
    offsetOverride: false,
    calculateDistance: false,
    originLat: 0,
    originLng: 0,
    destinationAddr: "",
    destinationLat: 0,
    destinationLng: 0,
    destinationSearchRadius: 1,
    timeToDestination: 0,
    parkingPeriod: 0.5,
    shortlists: []
  };

  componentDidMount = () => {
    new Availability(this.onDataLoadCallback);
    navigator.geolocation.getCurrentPosition((position) => {
      const originLat = position.coords.latitude;
      const originLng = position.coords.longitude;
      this.setState({ position, originLat, originLng });
    });
  }

  refreshCarparkShortlist = () => {
    if (this.state.destinationAddr === "") return;
    const carparkShortlist = new CarparkShortlist(
      this.state.originLat,
      this.state.originLng,
      this.state.destinationLat,
      this.state.destinationLng,
      this.state.destinationSearchRadius,
      this.state.availability);
    carparkShortlist.calculateParkingFee(this.state.parkingPeriod, this.state.timeToDestination);
    const shortlists = carparkShortlist.getCarparks();
    this.setState({ shortlists: shortlists }, () => {
      //console.log(this.state);
    });
  }

  onSelectDestination = (destinationData) => {
    this.setState({ offsetOverride: false, calculateDistance: true });
    this.setState(destinationData, this.refreshCarparkShortlist);
  }

  onChangeParkingPeriod = (parkingPeriod) => {
    this.setState({ parkingPeriod: parkingPeriod }, this.refreshCarparkShortlist);
  }

  onChangeOffset = (event) => {
    this.setState({ offsetOverride: true, timeToDestination: event.target.value }, this.refreshCarparkShortlist);
  }

  onChangeDMDistance = (distance) => {
    this.setState({ distance: distance }, this.refreshCarparkShortlist);
  }

  onChangeDMOffset = (offset) => {
    if (this.state.offsetOverride === true) return; // NOTE: when will offset be override
    this.setState({ timeToDestination: offset, calculateDistance: false }, this.refreshCarparkShortlist);
  }

  onDataLoadCallback = (availability) => {
    this.setState({ availability: availability }, this.refreshCarparkShortlist);
  }

  render() {
    return (
      <main>
        <div className="container-fluid bg-secondary pt-4 pb-2 px-0 mx-0">
          <div className="container">
            <div className="text-start text-md-center text-light pt-3 pb-3">
              <h1><span className="fw-bold fs-1">Nearest</span> parking</h1>
            </div>
            <PlacesAutocomplete
              inputs={this.state}
              onSelectDestination={this.onSelectDestination} />
            <DistanceMatrixWrapper
              inputs={this.state}
              onChangeDMDistance={this.onChangeDMDistance}
              onChangeDMOffset={this.onChangeDMOffset} />
          </div>
        </div>
        <div className="container-fluid p-0 m-0">
          <div className="container-lg py-2">
            <ParkingPeriod onChangeParkingPeriod={this.onChangeParkingPeriod} />
            <ParkingTimeOffset offset={this.state.timeToDestination} onChangeOffset={this.onChangeOffset} />
          </div>
          <ParkingList shortlists={this.state.shortlists} />
        </div>
      </main>
    );
  }
}

export default App;

