export class Parking {
    constructor(parkingRates, parkingBeginDateTime, parkingTimeInMinutes) {
        this.parkingRates = parkingRates;
        this.parkingBeginDateTime = parkingBeginDateTime;
        this.parkingTimeInMinutes = parkingTimeInMinutes;
        this.parkingStopDateTime = new Date(this.generateStopDateTime(this.parkingBeginDateTime, this.parkingTimeInMinutes));
        this.parkingFee = 0;
        this.parkingForbidden = false;
        this.parkingForbiddenBeginDateTime = null;
        this.detailParkingRates = [];

        this.populateParkingRates();
        this.calculateParkingFee();
    }

    generateStopDateTime = (startDateTime, timeInMinutes) => {
        return (new Date()).setTime(startDateTime.getTime() + timeInMinutes * 60 * 1000);
    }

    isDualParkingRate = (parkingRate) => {
        const rates = parkingRate.split('|');
        return (rates.length === 1) ? false : true;
    }

    parseParkingRate = (parkingRate, blockTimeInMinutes) => {
        const parkingRateFees = {};
        const subParkingRates = parkingRate.match(/[0-9.]{4}[a-z]{2,}/gi);
        subParkingRates.forEach(subParkingRate => {
            const logic = subParkingRate.match(/[a-z]{2,}/gi)[0];
            const amount = parseFloat(subParkingRate.match(/[0-9.]{4}/gi)[0]);
            switch (logic) {
                case 'pm': // per minute
                    parkingRateFees.coreFee = amount;
                    parkingRateFees.coreFeeTimeInMinutes = 1;
                    parkingRateFees.coreFeePerEntry = false;
                    break;
                case 'ph': // per hour
                    parkingRateFees.coreFee = amount;
                    parkingRateFees.coreFeeTimeInMinutes = 60;
                    parkingRateFees.coreFeePerEntry = false;
                    break;
                case 'pa': // per 2 hours
                    parkingRateFees.coreFee = amount;
                    parkingRateFees.coreFeeTimeInMinutes = 120;
                    parkingRateFees.coreFeePerEntry = false;
                    break;
                case 'pb': // per 3 hours
                    parkingRateFees.coreFee = amount;
                    parkingRateFees.coreFeeTimeInMinutes = 180;
                    parkingRateFees.coreFeePerEntry = false;
                    break;
                case 'pc': // per 4 hours
                    parkingRateFees.coreFee = amount;
                    parkingRateFees.coreFeeTimeInMinutes = 240;
                    parkingRateFees.coreFeePerEntry = false;
                    break;
                case 'pd': // per 30 minutes
                    parkingRateFees.coreFee = amount;
                    parkingRateFees.coreFeeTimeInMinutes = 30;
                    parkingRateFees.coreFeePerEntry = false;
                    break;
                case 'pq': // per 15 minutes
                    parkingRateFees.coreFee = amount;
                    parkingRateFees.coreFeeTimeInMinutes = 15;
                    parkingRateFees.coreFeePerEntry = false;
                    break;
                case 'ps': // per 10 minutes
                    parkingRateFees.coreFee = amount;
                    parkingRateFees.coreFeeTimeInMinutes = 10;
                    parkingRateFees.coreFeePerEntry = false;
                    break;
                case 'pe': // per entry
                    parkingRateFees.coreFee = amount;
                    parkingRateFees.coreFeeTimeInMinutes = blockTimeInMinutes;
                    parkingRateFees.coreFeePerEntry = true;
                    break;
                case 'fh': // first hour
                    parkingRateFees.firstFee = amount;
                    parkingRateFees.firstFeeTimeInMinutes = 60;
                    parkingRateFees.firstUsage = false;
                    break;
                case 'fd': // first 2 hours
                    parkingRateFees.firstFee = amount;
                    parkingRateFees.firstFeeTimeInMinutes = 120;
                    parkingRateFees.firstUsage = false;
                    break;
                case 'ft': // first 3 hours
                    parkingRateFees.firstFee = amount;
                    parkingRateFees.firstFeeTimeInMinutes = 180;
                    parkingRateFees.firstUsage = false;
                    break;
                case 'fq': // first 4 hours
                    parkingRateFees.firstFee = amount;
                    parkingRateFees.firstFeeTimeInMinutes = 240;
                    parkingRateFees.firstUsage = false;
                    break;
                case 'ff': // first 5 hours
                    parkingRateFees.firstFee = amount;
                    parkingRateFees.firstFeeTimeInMinutes = 300;
                    parkingRateFees.firstUsage = false;
                    break;
                case 'fa': // first 30 mins
                    parkingRateFees.firstFee = amount;
                    parkingRateFees.firstFeeTimeInMinutes = 30;
                    parkingRateFees.firstUsage = false;
                    break;
                case 'fb': // first 1.5 hours
                    parkingRateFees.firstFee = amount;
                    parkingRateFees.firstFeeTimeInMinutes = 90;
                    parkingRateFees.firstUsage = false;
                    break;
                case 'fc': // first 2.5 hours
                    parkingRateFees.firstFee = amount;
                    parkingRateFees.firstFeeTimeInMinutes = 150;
                    parkingRateFees.firstUsage = false;
                    break;
                case 'sh': // second hour
                    parkingRateFees.secondFee = amount;
                    parkingRateFees.secondFeeTimeInMinutes = 60;
                    parkingRateFees.secondFeeLenInMinutes = 60;
                    parkingRateFees.secondUsage = false;
                    break;
                case 'pqsd': // per 15 min in next 2 hours
                    parkingRateFees.secondFee = amount;
                    parkingRateFees.secondFeeTimeInMinutes = 15;
                    parkingRateFees.secondFeeLenInMinutes = 120;
                    parkingRateFees.secondUsage = false;
                    break;
                case 'phsd': // per hour in next 2 hours
                    parkingRateFees.secondFee = amount;
                    parkingRateFees.secondFeeTimeInMinutes = 60;
                    parkingRateFees.secondFeeLenInMinutes = 120;
                    parkingRateFees.secondUsage = false;
                    break;
                case 'sc': // surcharge
                    parkingRateFees.surchargeFee = amount;
                    break;
                case 'sp': // season parking only
                    parkingRateFees.coreFee = 0;
                    parkingRateFees.coreFeeTimeInMinutes = blockTimeInMinutes;
                    parkingRateFees.coreFeePerEntry = false;
                    parkingRateFees.seasonParkingOnly = true;
                    break;
                case 'np': // no parking
                    parkingRateFees.coreFee = 0;
                    parkingRateFees.coreFeeTimeInMinutes = blockTimeInMinutes;
                    parkingRateFees.coreFeePerEntry = false;
                    parkingRateFees.noParking = true;
                    break;
                case 'mx': // maximum
                    parkingRateFees.maximumFee = amount;
                    break;
                default:
                    break;
            }
        });

        return parkingRateFees;
    }

    populateParkingRate = (parkingRate, blockSize, beginParkingRate, blockTimeInMinutes, toStopTimeInMinutes) => {
        const detailParkingRateMeta = {};
        detailParkingRateMeta.name = parkingRate;
        detailParkingRateMeta.blockSize = blockSize;
        detailParkingRateMeta.beginDateTime = beginParkingRate;
        detailParkingRateMeta.stopDateTime = new Date(this.generateStopDateTime(beginParkingRate, blockTimeInMinutes));
        detailParkingRateMeta.blockTimeInMinutes = blockTimeInMinutes;
        detailParkingRateMeta.toStopTimeInMinutes = toStopTimeInMinutes;
        const detailParkingRateFees = this.parseParkingRate(parkingRate, blockTimeInMinutes);
        const detailParkingRate = {
            ...detailParkingRateMeta,
            ...detailParkingRateFees
        };
        this.detailParkingRates.push(detailParkingRate);
        return detailParkingRate.stopDateTime;
    }

    populateParkingRates = () => {
        let beginParkingRate = this.parkingBeginDateTime;
        let toStopTimeInMinutes = this.parkingTimeInMinutes;
        let blockTimeInMinutes = ((this.isDualParkingRate(this.parkingRates[0])) ? 30 : 60) - this.parkingBeginDateTime.getMinutes();
        for (let i = 0; i < this.parkingRates.length; ++i) {
            if (toStopTimeInMinutes === 0) break;

            const parkingRate = this.parkingRates[i];
            const rates = parkingRate.split('|');

            let nextBlockSize = 60;
            if (this.parkingRates[i + 1] !== undefined && this.isDualParkingRate(this.parkingRates[i + 1])) {
                nextBlockSize = 30;
            }

            if (!this.isDualParkingRate(parkingRate)) {
                beginParkingRate = this.populateParkingRate(rates[0], 60, beginParkingRate, blockTimeInMinutes, toStopTimeInMinutes);
                toStopTimeInMinutes = toStopTimeInMinutes - blockTimeInMinutes;
                blockTimeInMinutes = (toStopTimeInMinutes > nextBlockSize) ? nextBlockSize : toStopTimeInMinutes;
            } else {
                if (beginParkingRate.getMinutes() < 30) {
                    beginParkingRate = this.populateParkingRate(rates[0], 30, beginParkingRate, blockTimeInMinutes, toStopTimeInMinutes);
                    toStopTimeInMinutes = toStopTimeInMinutes - blockTimeInMinutes;
                    blockTimeInMinutes = (toStopTimeInMinutes > 30) ? 30 : toStopTimeInMinutes;
                } else {
                    blockTimeInMinutes += 30;
                }

                if (toStopTimeInMinutes === 0) break;
                beginParkingRate = this.populateParkingRate(rates[1], 30, beginParkingRate, blockTimeInMinutes, toStopTimeInMinutes);
                toStopTimeInMinutes = toStopTimeInMinutes - blockTimeInMinutes;
                blockTimeInMinutes = (toStopTimeInMinutes > nextBlockSize) ? nextBlockSize : toStopTimeInMinutes;
            }
        }
    }

    calculateParkingFee = () => {
        for (let i = 0, subtotalFee = 0, prevRateName = ''; i < this.detailParkingRates.length; ++i) {
            const detailParkingRate = this.detailParkingRates[i];
            if (prevRateName !== detailParkingRate.name) {
                detailParkingRate.type = 'new';
                this.parkingFee += subtotalFee;
                subtotalFee = 0;

                if (detailParkingRate.surchargeFee !== undefined && detailParkingRate.surchargeFee > 0) {
                    subtotalFee += detailParkingRate.surchargeFee;
                }

                if (detailParkingRate.firstFee !== undefined) {
                    subtotalFee += detailParkingRate.firstFee;
                    detailParkingRate.firstUsage = true;
                    if (detailParkingRate.firstFeeTimeInMinutes > detailParkingRate.blockTimeInMinutes) {
                        detailParkingRate.extra = detailParkingRate.firstFeeTimeInMinutes - detailParkingRate.blockTimeInMinutes;
                    } else {
                        let periodInBlock = detailParkingRate.blockTimeInMinutes - detailParkingRate.firstFeeTimeInMinutes;
                        let multiplier = Math.ceil(periodInBlock / detailParkingRate.coreFeeTimeInMinutes);
                        let period = multiplier * detailParkingRate.coreFeeTimeInMinutes;
                        let amount = detailParkingRate.coreFee * multiplier;
                        subtotalFee += amount;
                        detailParkingRate.extra = period - periodInBlock;
                    }
                } else { // NOTE: this includes the per entry fee
                    //let periodInBlock = detailParkingRate.blockTimeInMinutes; // NOTE: Fix for the 30 minutes parking
                    let periodInBlock = (detailParkingRate.blockTimeInMinutes < detailParkingRate.toStopTimeInMinutes) ? detailParkingRate.blockTimeInMinutes : detailParkingRate.toStopTimeInMinutes;
                    let multiplier = Math.ceil(periodInBlock / detailParkingRate.coreFeeTimeInMinutes);
                    let period = multiplier * detailParkingRate.coreFeeTimeInMinutes;
                    let amount = detailParkingRate.coreFee * multiplier;
                    subtotalFee += amount;
                    detailParkingRate.extra = period - periodInBlock;
                }

            } else {
                detailParkingRate.type = 'continue';

                if (this.detailParkingRates[i - 1].firstUsage !== undefined && this.detailParkingRates[i - 1].firstUsage === true) {
                    detailParkingRate.firstUsage = true;
                }

                if (this.detailParkingRates[i - 1].secondUsage !== undefined && this.detailParkingRates[i - 1].secondUsage === true) {
                    detailParkingRate.secondUsage = true;
                }

                if (detailParkingRate.coreFeePerEntry === true) {
                    // NOTE: do nothing if it is per entry fee
                } else {
                    let prevExtra = this.detailParkingRates[i - 1].extra;
                    if (detailParkingRate.secondUsage !== undefined && detailParkingRate.secondUsage === false && detailParkingRate.firstUsage === true) {
                        if (prevExtra < detailParkingRate.blockTimeInMinutes) {
                            // NOTE: activate second fee
                            let periodToEnd = ((detailParkingRate.secondFeeLenInMinutes < detailParkingRate.toStopTimeInMinutes) ? detailParkingRate.secondFeeLenInMinutes : detailParkingRate.toStopTimeInMinutes);
                            let periodInBlock = detailParkingRate.blockTimeInMinutes - prevExtra;
                            let multiplier = Math.ceil(periodToEnd / detailParkingRate.secondFeeTimeInMinutes);
                            let period = multiplier * detailParkingRate.secondFeeTimeInMinutes;
                            let amount = detailParkingRate.secondFee * multiplier;
                            subtotalFee += amount;
                            detailParkingRate.extra = period - periodInBlock;
                            detailParkingRate.secondUsage = true;
                        } else {
                            detailParkingRate.extra = prevExtra - detailParkingRate.blockTimeInMinutes;
                        }
                    } else {
                        if (prevExtra < detailParkingRate.blockTimeInMinutes) {
                            let periodInBlock = detailParkingRate.blockTimeInMinutes - prevExtra;
                            let multiplier = Math.ceil(periodInBlock / detailParkingRate.coreFeeTimeInMinutes);
                            let period = multiplier * detailParkingRate.coreFeeTimeInMinutes;
                            let amount = detailParkingRate.coreFee * multiplier;
                            subtotalFee += amount;
                            detailParkingRate.extra = period - periodInBlock;
                        } else {
                            detailParkingRate.extra = prevExtra - detailParkingRate.blockTimeInMinutes;
                        }
                    }
                }
            }

            if (detailParkingRate.maximumFee !== undefined && detailParkingRate.maximumFee > 0) {
                let maximumFee = detailParkingRate.maximumFee;
                subtotalFee = (subtotalFee > maximumFee) ? maximumFee : subtotalFee;
            }

            if (this.parkingForbidden === false && (detailParkingRate.seasonParkingOnly === true || detailParkingRate.noParking === true)) {
                this.parkingForbidden = true;
                this.parkingForbiddenBeginDateTime = detailParkingRate.beginDateTime;
            }

            if ((i + 1) === this.detailParkingRates.length) {
                this.parkingFee += subtotalFee;
            }

            prevRateName = this.detailParkingRates[i].name;
        }
    }
}
