import React, { Component } from 'react';

class ParkingTimeOffset extends Component {

  constructor() {
    super();
    this.offsets = [
      { value: 0, description: 'arrive now' },
      { value: 5, description: 'arrive in +5min' },
      { value: 10, description: 'arrive in +10min' },
      { value: 15, description: 'arrive in +15min' },
      { value: 20, description: 'arrive in +20min' },
      { value: 25, description: 'arrive in +25min' },
      { value: 30, description: 'arrive in +30min' },
      { value: 35, description: 'arrive in +35min' },
      { value: 40, description: 'arrive in +40min' },
      { value: 45, description: 'arrive in +45min' }
    ];
  }

  renderOffsetOptions = () => {
    this.offsets.map((offset) => {
      return (
        <option value={offset.value}>{offset.description}</option>
      );
    });
  }

  render() {
    return (
      <div className="col-xs-2">
        <select className="form-select fs-5 bg-warning" style={{ width: 200 }} value={this.props.offset} onChange={this.props.onChangeOffset}>
          {this.offsets.map(offset => (
            <option key={offset.value} value={offset.value}>{offset.description}</option>
          ))}
        </select>
      </div>
    );
  }
}

export default ParkingTimeOffset;