import Carparks from "../data.json";
import { Parking } from "./Parking";

export class CarparkShortlist {
  constructor(originLat, originLng, destinationLat, destinationLng, maxDistance, carparkAvailability) {
    this.originLat = originLat;
    this.originLng = originLng;
    this.destinationLat = destinationLat;
    this.destinationLng = destinationLng;
    this.maxDistance = maxDistance;
    this.carparkAvailablity = carparkAvailability;
    this.walkingSpeed = 3; // NOTE: this is 3 km per hour
    this.shortlists = [];

    this.populateShortlistedCarparks();
    this.populateCarparkAvailability();
    this.sortShortlistsByDistance(this.shortlists);
  }

  getDistanceFromLatLngInKm = (lat1, lng1, lat2, lng2) => {
    const deg2rad = (deg) => {
      return deg * (Math.PI / 180);
    }
    const R = 6371; // Radius of the earth in km
    const dLat = deg2rad(lat2 - lat1);
    const dLng = deg2rad(lng2 - lng1);
    const a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos(deg2rad(lat1)) * Math.cos(deg2rad(lat2)) *
      Math.sin(dLng / 2) * Math.sin(dLng / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    const d = R * c; // Distance in km
    return d;
  }

  sorterLogic = (a, b) => {
    return a.distance - b.distance;
  }

  sortShortlistsByDistance = (arr) => {
    if (arr === undefined || arr.length < 1) return;
    arr.sort(this.sorterLogic);
  }

  generateGoogleMapUrl = (carparkLat, carparkLng) => {
    const origin = "&origin=" + this.originLat + "," + this.originLng;
    const destination = "&destination=" + carparkLat + "," + carparkLng;
    const travelMode = "&travelmode=driving";
    const googleMapUrl = "https://www.google.com/maps/dir/?api=1" + origin + destination + travelMode;
    return googleMapUrl;
  }

  populateShortlistedCarparks = () => {
    Carparks.map((carpark) => {
      const googleMapUrl = this.generateGoogleMapUrl(carpark.lat, carpark.lng);
      const distance = this.getDistanceFromLatLngInKm(this.destinationLat, this.destinationLng, carpark.lat, carpark.lng);
      const walkingMinutes = Math.ceil(distance / this.walkingSpeed * 60);
      if (distance < this.maxDistance) {
        this.shortlists.push({ carpark, distance, walkingMinutes, googleMapUrl });
      }
      return true;
    });
  }

  populateCarparkAvailability = () => {
    if (this.carparkAvailablity === undefined) return;
    this.shortlists.forEach(shortlist => {
      const carparkNo = shortlist.carpark.car_park_no;
      this.carparkAvailablity.data.forEach(availability => {
        const availabilityCarparkNo = availability.carpark_number;
        if (carparkNo === availabilityCarparkNo) {
          shortlist.carpark.carparkAvailability = availability.carpark_info;
        }
      })
    });
  }

  calculateParkingFee = (period, offset) => {
    const startParking = new Date(Date.now() + offset * 60 * 1000);
    const parkingPeriod = period * 60;
    const day = startParking.getDay(); // NOTE: Sunday=0, Monday=1, ...
    const hour = startParking.getHours();
    const beginIndex = day * 24 + hour;
    const endIndex = beginIndex + Math.ceil(period) + 2;
    this.shortlists.forEach(shortlist => {
      const carparkRatesRaw = [...shortlist.carpark.carpark_rates, ...shortlist.carpark.carpark_rates];
      const carparkRates = carparkRatesRaw.slice(beginIndex, endIndex);
      shortlist.fee_structure = new Parking(carparkRates, startParking, parkingPeriod);
    });
  }

  getCarparks = () => {
    return this.shortlists;
  }
}